/* The container */
#notifications-settings .checkbox {
  display: block;
  position: relative;
  padding-left: 35px;
  margin-bottom: 12px;
  margin-left: 25px;
  cursor: pointer;
  font-size: 17px;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}

/* Hide the browser's default checkbox */
#notifications-settings .checkbox input {
  position: absolute;
  opacity: 0;
  cursor: pointer;
  height: 0;
  width: 0;
}

/* Create a custom checkbox */
#notifications-settings .checkmark {
  background-color: #FFF;
  border: 1px solid #C1C3C6;
  display: block;
  position: absolute;
  top: 1px;
  left: 0;
  width: 1.25em;
  height: 1.25em;
  border-radius: 4px;
}
/* When the checkbox is checked, add a blue background */
#notifications-settings .checkbox input:checked ~ .checkmark {
  background-color: #1DB954;
}

/* Create the checkmark/indicator (hidden when not checked) */
#notifications-settings .checkmark:after {
  content: "";
  position: absolute;
  display: none;
}

/* Show the checkmark when checked */
#notifications-settings .checkbox input:checked ~ .checkmark:after {
  display: block;
}

/* Style the checkmark/indicator */
#notifications-settings .checkbox .checkmark:after {
  left: 6px;
  top: 3px;
  width: 7px;
  height: 10px;
  border: solid white;
  border-width: 0 3px 3px 0;
  transform: rotate(45deg);
}
